.modal-container {
  display: flex;
  flex-direction: column;
  background: rgba(240, 240, 245, 0.7);
  backdrop-filter: blur(60px) saturate(200%);
  -webkit-backdrop-filter: blur(60px) saturate(200%);
  border: 1px solid rgba(255, 255, 255, 0.3);
  box-shadow: 
    0 25px 75px rgba(0, 0, 0, 0.35),
    0 0 0 0.5px rgba(255, 255, 255, 0.4) inset,
    0 1px 2px rgba(255, 255, 255, 0.3) inset;
  border-radius: 10px;
  overflow: hidden;
  position: relative;
  
  &::before {
    content: '';
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    background: linear-gradient(
      135deg,
      rgba(255, 255, 255, 0.1) 0%,
      rgba(255, 255, 255, 0.05) 50%,
      rgba(255, 255, 255, 0.02) 100%
    );
    pointer-events: none;
    border-radius: 10px;
  }
}

.container-header {
  display: flex;
  padding: 10px 16px;
  cursor: move;
  align-items: center;
  justify-content: flex-start;
  gap: 12px;
  min-height: 40px;
  background: linear-gradient(180deg, 
    rgba(255, 255, 255, 0.85) 0%, 
    rgba(248, 248, 250, 0.8) 50%,
    rgba(246, 246, 248, 0.75) 100%);
  backdrop-filter: blur(20px) saturate(180%);
  -webkit-backdrop-filter: blur(20px) saturate(180%);
  border-bottom: 1px solid rgba(0, 0, 0, 0.08);
  position: relative;
  
  /* iframe 콘텐츠 초과 드래그 개선 */
  touch-action: none;
  user-select: none;
  -webkit-user-select: none;
  -webkit-user-drag: none;
  
  &::after {
    content: '';
    position: absolute;
    bottom: 0;
    left: 0;
    right: 0;
    height: 1px;
    background: linear-gradient(
      90deg,
      transparent 0%,
      rgba(255, 255, 255, 0.6) 50%,
      transparent 100%
    );
  }
}

.container-header:active {
  cursor: grabbing;
}

/* macOS 스타일 윈도우 컨트롤 버튼 */
.window-controls-left {
  display: flex;
  gap: 8px;
  align-items: center;
  z-index: 1;
}

.control-button {
  width: 12px;
  height: 12px;
  border-radius: 50%;
  border: 0.5px solid rgba(0, 0, 0, 0.15);
  cursor: pointer;
  display: flex;
  align-items: center;
  justify-content: center;
  padding: 0;
  transition: all 0.15s ease;
  position: relative;
  box-shadow: 
    0 1px 2px rgba(0, 0, 0, 0.15),
    inset 0 1px 0 rgba(255, 255, 255, 0.4);
}

.control-button svg {
  width: 6px;
  height: 6px;
  opacity: 0;
  transition: opacity 0.15s ease;
}

.container-header:hover .control-button svg {
  opacity: 0.7;
}

.control-button:hover svg {
  opacity: 1 !important;
}

.control-button.close {
  background: linear-gradient(135deg, #ff6057 0%, #ff5f56 100%);
}

.control-button.close:hover {
  background: linear-gradient(135deg, #ff4b42 0%, #ff3b30 100%);
  box-shadow: 
    0 2px 4px rgba(255, 59, 48, 0.4),
    inset 0 1px 0 rgba(255, 255, 255, 0.5);
}

.control-button.close:active {
  background: linear-gradient(135deg, #d93025 0%, #c9211e 100%);
}

.control-button.minimize {
  background: linear-gradient(135deg, #ffbd2e 0%, #ffbc2d 100%);
}

.control-button.minimize:hover {
  background: linear-gradient(135deg, #ffb01f 0%, #ffa900 100%);
  box-shadow: 
    0 2px 4px rgba(255, 169, 0, 0.4),
    inset 0 1px 0 rgba(255, 255, 255, 0.5);
}

.control-button.minimize:active {
  background: linear-gradient(135deg, #e09b00 0%, #d08800 100%);
}

.control-button.maximize {
  background: linear-gradient(135deg, #28ca42 0%, #27c93f 100%);
}

.control-button.maximize:hover {
  background: linear-gradient(135deg, #20b038 0%, #1fa832 100%);
  box-shadow: 
    0 2px 4px rgba(31, 168, 50, 0.4),
    inset 0 1px 0 rgba(255, 255, 255, 0.5);
}

.control-button.maximize:active {
  background: linear-gradient(135deg, #1a9930 0%, #178528 100%);
}

.header-label {
  position: absolute;
  left: 50%;
  transform: translateX(-50%);
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
  max-width: calc(100% - 200px);
  font-weight: 600;
  font-size: 13px;
  color: #ffffff;
  text-align: center;
  letter-spacing: 0.2px;

  .folder-tag {
    margin-left: 6px;
    opacity: 0.7;
    font-size: 12px;
    font-weight: 500;
    color: rgba(255, 255, 255, 0.85);
  }
}

.spacer {
  flex: 1;
}

.container-body {
  overflow: auto;
  flex: 1;
  padding: 0;
  margin: 0;
  background: linear-gradient(
    180deg,
    rgba(255, 255, 255, 0.9) 0%,
    rgba(252, 252, 254, 0.85) 100%
  );
  backdrop-filter: blur(20px);
  -webkit-backdrop-filter: blur(20px);
  position: relative;
}

/* iframe 스타일 */
.app-iframe {
  width: 100%;
  height: 100%;
  border: none;
  display: block;
  background: white;
}

/* 라우트 플레이스홀더 */
.route-placeholder {
  width: 100%;
  height: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
  background: linear-gradient(135deg, #667eea 0%, #764ba2 100%);
}

.placeholder-content {
  text-align: center;
  color: white;
  padding: 40px;
}

.placeholder-icon {
  width: 80px;
  height: 80px;
  margin: 0 auto 20px;
  stroke: white;
  stroke-width: 1.5;
  filter: drop-shadow(0 4px 8px rgba(0, 0, 0, 0.2));
}

.placeholder-content h3 {
  margin: 0 0 10px 0;
  font-size: 24px;
  font-weight: 600;
  text-shadow: 0 2px 4px rgba(0, 0, 0, 0.2);
}

.placeholder-content p {
  margin: 5px 0;
  font-size: 14px;
  opacity: 0.9;
}

.info-text {
  margin-top: 20px !important;
  font-size: 12px !important;
  opacity: 0.7 !important;
  font-style: italic;
}

/* macOS 스크롤바 스타일 */
.container-body::-webkit-scrollbar {
  width: 8px;
  height: 8px;
}

.container-body::-webkit-scrollbar-track {
  background: transparent;
}

.container-body::-webkit-scrollbar-thumb {
  background: rgba(0, 0, 0, 0.2);
  border-radius: 4px;
  
  &:hover {
    background: rgba(0, 0, 0, 0.3);
  }
}

/* 다크모드 지원 */
@media (prefers-color-scheme: dark) {
  .modal-container {
    background: rgba(40, 40, 50, 0.7);
    border-color: rgba(255, 255, 255, 0.15);
    box-shadow: 
      0 25px 75px rgba(0, 0, 0, 0.6),
      0 0 0 0.5px rgba(255, 255, 255, 0.2) inset,
      0 1px 2px rgba(255, 255, 255, 0.1) inset;
      
    &::before {
      background: linear-gradient(
        135deg,
        rgba(255, 255, 255, 0.05) 0%,
        rgba(255, 255, 255, 0.02) 50%,
        rgba(255, 255, 255, 0.01) 100%
      );
    }
  }

  .container-header {
    background: linear-gradient(180deg, 
      rgba(55, 55, 65, 0.9) 0%,
      rgba(45, 45, 55, 0.85) 50%, 
      rgba(40, 40, 50, 0.8) 100%);
    border-bottom-color: rgba(255, 255, 255, 0.08);
    
    &::after {
      background: linear-gradient(
        90deg,
        transparent 0%,
        rgba(255, 255, 255, 0.1) 50%,
        transparent 100%
      );
    }
  }

  .header-label {
    color: rgba(255, 255, 255, 0.9);
  }

  .container-body {
    background: linear-gradient(
      180deg,
      rgba(30, 30, 40, 0.9) 0%,
      rgba(25, 25, 35, 0.85) 100%
    );
  }

  .container-body::-webkit-scrollbar-thumb {
    background: rgba(255, 255, 255, 0.2);
    
    &:hover {
      background: rgba(255, 255, 255, 0.3);
    }
  }
}